const pyCode = `
MAXN = int(1e5)
flag = [True] * (MAXN + 10) # if flag[i] is true, then i is prime
flag[0], flag[1] = False, False
for i in range(2, int(MAXN ** 0.5) + 1):
    if flag[i]:
        for j in range(i * 2, MAXN + 1, i):
            flag[j] = False
`

